import React, { Component } from 'react';
import { HashRouter as Router, Route, Switch} from "react-router-dom";
import './App.css';
import NewGroup from "./components/NewGroup.js"
import About from "./components/About.js"
import Landing from "./components/Landing.js"
import GroupPage from "./components/GroupPage.js"

const apiUrl = "https://travelsquadback.herokuapp.com/api"

class App extends Component {

  state = {
      users: [],
      groups: [],
      dates: [],
      airbnbs: [],
  }

  componentDidMount() {
    this.getData()
  }

  getData = () => {
    fetch(apiUrl)
    .then(res => res.json())
    .then(res => {
    this.setState({
      users: res.users,
      groups: res.groups,
      dates: res.dates,
      airbnbs: res.airbnb
    })
  })
    .catch(error => console.error('Error:', error))
    .then(response => console.log('Success:', this.state))
    }

 render() {
   return (
       <Router>
         <div className="App">
           <Switch>
             <Route exact path="/" component={Landing} />
             <Route path="/new" render={()=><NewGroup 
               users={this.state.users} 
               groups={this.state.groups}/>} />
             <Route path="/group/:id" render={()=><GroupPage 
               airbnbs={this.state.airbnbs} 
               dates={this.state.dates} 
               users={this.state.users} 
               groups={this.state.groups}/>} />
           </Switch>
         </div>
       </Router>
   )
 }
}

export default App;



